<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process118"></a>

<p><br></p>
<p><span class="rvts17">支线：维泽磨的灵柩罐（</span><span class="rvts50">Withermoor's Soul Jar</span><span class="rvts17">）</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:496,Y:554</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：莫迪（</span><span class="rvts20">Mody</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">释放维泽磨的灵魂</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1028.jpg" display="block" background-size="contain|cover"><span class="rvts20"></span>
<br><span class="rvts21">首先和贫民窟洞穴中的莫迪谈话，注意语气，不然无法触发和他捉迷藏的剧情。</span>
<br><span class="rvts21">第一次捉迷藏，只需要跟着莫迪就可以找到他。</span>
<br><span class="rvts21">第二次捉迷藏，莫迪这小家伙隐身了，需要在下面的位置找到他，并发现维泽磨君王的秘密。另外，如果队伍中有野兽，那么由于他是矮人，可以直接钻进地洞找到卫泽莫尔。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1029.jpg" display="block" background-size="contain|cover"><span class="rvts20"></span>
<br><span class="rvts21">第二次捉迷藏在这里找到莫迪，莫迪钻进山石另外一边，我们的需要使用铁锨（或有蜥蜴角色队友），进行挖掘沙土后发现秘门。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1030.jpg" display="block" background-size="contain|cover"><span class="rvts20"></span>
<br><span class="rvts21">进入秘门后会发现维泽磨被死死钉在了墙上，队伍种需要有一名力量属性在</span><span class="rvts20">12</span><span class="rvts21">点以上的角色，用来移除他身上的长矛。</span>
<br><span class="rvts21">之后维泽磨会求助帮他找到他的灵柩罐，并毁灭灵柩罐，释放他的灵魂。他会告诉在监狱里一处隐藏的远古通道。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1031.jpg" display="block" background-size="contain|cover"><span class="rvts20"></span>
<br><span class="rvts21">可以通过很多方式进入欢乐堡监狱，最简单的方法就是在楼上写到的那段</span><span class="rvts20"></span><span class="rvts21">蹊跷的谋杀（</span><span class="rvts20">The Murderous Gheist</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线任务种提到的米果的沙滩位置不远处的小路边，</span>
<br><span class="rvts21">有一处被沙土隐藏的楼梯门，从那里下去进入地牢并再进入监狱。</span>
<br><span class="rvts21">如图，在监狱的这个位置（坐标：</span><span class="rvts20">X:316,Y:672</span><span class="rvts21">），拉动隐藏的杠杆，触发秘密的远古通道的大门。（如果之前你没有拔下维泽磨身上的长矛，那这里就不会出现杠杆）</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1032.jpg" display="block" background-size="contain|cover"><span class="rvts20"></span>
<br><span class="rvts21">进入远古通道密室，会遇到两处散毒的陷阱，可以通过使用旁边的瓦盆，瓦罐进行填堵，或使用火焰灭了毒后迅速通过（毒气还会刷新）。最后来到这里放了有</span><span class="rvts20">5</span><span class="rvts21">处灵柩的地方，尽可能的寻找标有</span><span class="rvts20">揥ithermoor's Supplicant</span><span class="rvts21">标示的灵柩，因为其它</span><span class="rvts20">4</span><span class="rvts21">处都是陷阱灵柩。</span>
<br><span class="rvts21">如果等级较低，建议在这个地方尽可能的不要选择错灵柩。等级高的话，可以与其它的假灵柩触发出来的敌人进行一战。</span>
<br><span class="rvts21">破坏掉维泽磨的灵柩后，维泽磨的灵魂得到释放，石化的躯体便会死去，该任务会暂时到一段落。之后我们会在雷克斯塔遇到维泽磨，并得到他的帮助后，该任务将关闭。（详见后续章节</span><span class="rvts20"></span><span class="rvts21">神龙迷宫</span><span class="rvts20"></span><span class="rvts21">支线）</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



